import mFooterCar from '@/components/m-footer-car' // 底部购物车
import mStorePro from '@/components/m-store-pro' // 商品列表
import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue" // 加减数字
import categroyList from '@/common/mock.js'
import {
	mapState,
	mapGetters,
	mapActions
} from 'vuex'
// 抛物线计算
function bezier(pots, amount) {
	var pot;
	var lines;
	var ret = [];
	var points;
	for (var i = 0; i <= amount; i++) {
		points = pots.slice(0);
		lines = [];
		while (pot = points.shift()) {
			if (points.length) {
				lines.push(pointLine([pot, points[0]], i / amount));
			} else if (lines.length > 1) {
				points = lines;
				lines = [];
			} else {
				break;
			}
		}
		ret.push(lines[0]);
	}

	function pointLine(points, rate) {
		var pointA, pointB, pointDistance, xDistance, yDistance, tan, radian, tmpPointDistance;
		var ret = [];
		pointA = points[0]; //点击
		pointB = points[1]; //中间
		xDistance = pointB.x - pointA.x;
		yDistance = pointB.y - pointA.y;
		pointDistance = Math.pow(Math.pow(xDistance, 2) + Math.pow(yDistance, 2), 1 / 2);
		tan = yDistance / xDistance;
		radian = Math.atan(tan);
		tmpPointDistance = pointDistance * rate;
		ret = {
			x: pointA.x + tmpPointDistance * Math.cos(radian),
			y: pointA.y + tmpPointDistance * Math.sin(radian)
		};
		return ret;
	}
	return {
		'bezier_points': ret
	};
}
export default {
	components: {
		mStorePro,
		uniNumberBox,
		mFooterCar
	},
	  onShareAppMessage(res) {
	    if (res.from === 'button') {// 来自页面内分享按钮
	      console.log(res.target)
	    }
	    return {
	      title: '月仙宜家商行',
	      path: '/pages/index/index'
	    }
	  },
	data() {
		return {
			tapCount: 0,
			// 购物车小球动画start
			hide_good_box: false,
			bus_x: 0 - 0,
			bus_y: 0 - 100,
			finger: {},
			busPos: {},
			linePos: [],
			timer: null,
			// 购物车小球动画end
			carPrice: "0", //加入到购物车的总价钱
			carNum: 0, //加入到购物车的总个数


			specClass: '', //规格弹窗css类，控制开关动画
			showCategoryIndex: 0,
			//商品分类信息列表
			categoryList: categroyList
		}
	},
	computed: {
		...mapGetters(['cartTotalCount', 'cartTotalPrice']),
		...mapState({
			cartList: state => state.cart.list
		})
	},
	methods: {
		...mapActions('cart', ['deleteCartItem']),
		//分类切换显示
		showCategory(index) {
			this.showCategoryIndex = index;
		},
		// popup start
		//关闭规格弹窗
		hideSpec() {
			this.specClass = 'hide';
			//回调

			this.selectSpec && this.specCallback && this.specCallback();
			this.specCallback = false;
			setTimeout(() => {
				this.specClass = 'none';
			}, 200);
		},
		//规格弹窗
		showSpec(fun) {
			this.specClass = 'show';
			this.specCallback = fun;
		},
		specCallback() {
			return;
		},
		discard() {

		},
		// popup end
		// 加入购物车start
		touchOnGoods(obj) {
			const e = obj.element;
			const data = obj.data;
			if (this.timer) { // 清除一下动画
				clearInterval(this.timer);
			}
			this.finger = {};
			var topPoint = {};
			this.finger['x'] = e.target.x; //点击的位置
			this.finger['y'] = e.target.y;
			if (this.finger['y'] < this.busPos['y']) {
				topPoint['y'] = this.finger['y'] - 150;
			} else {
				topPoint['y'] = this.busPos['y'] - 150;
			}
			topPoint['x'] = Math.abs(this.finger['x'] - this.busPos['x']) / 2;

			if (this.finger['x'] > this.busPos['x']) {
				topPoint['x'] = (this.finger['x'] - this.busPos['x']) / 2 + this.busPos['x'];
			} else { //
				topPoint['x'] = (this.busPos['x'] - this.finger['x']) / 2 + this.finger['x'];
			}
			this.linePos = bezier([this.busPos, topPoint, this.finger], 30);
			this.startAnimation(e, data);
		},
		startAnimation: function(e, data) {
			var index = 0,
				that = this,
				bezier_points = that.linePos['bezier_points'];
			that.hide_good_box = false
			that.bus_x = that.finger['x'];
			that.bus_y = that.finger['y'];
			var len = bezier_points.length;
			index = len;
			that.timer = setInterval(function() {
				index--;
				that.bus_x = bezier_points[index]['x'];
				that.bus_y = bezier_points[index]['y'];
				if (index < 1) {
					clearInterval(that.timer);
					that.hide_good_box = true;
					that.addGoodSum(e, data)
				}
			}, 25);
		},

		// 购物车价钱与个数计算， 根据你的需求计算

		addGoodSum(e, data) {
			this.$store.dispatch('cart/addToCart', data)
		},
		//清空购物车
		clearCar() {
			this.$store.dispatch('cart/cleanCart')
		},
		phoneCall(number) {
			uni.makePhoneCall({
				phoneNumber: number //仅为示例
			});
		},
		imgTap(){
			this.tapCount++
			if(this.tapCount === 10){
				uni.showToast({
					title: 'i love you',
					duration: 5000
				})
			}
			}

	},

	onLoad() {
		let hh = 0;
		const that = this;
		that.busPos['x'] = 45; //购物车的位置






		wx.getSystemInfo({
			success: function(res) {
				hh = res.windowHeight;
				that.busPos['y'] = hh - 56;
			}
		})

	}

}
